<template>
    <div class="item-body">

        <div class="show-item">

            <figure class="view-item">
                <img class="image-item" :src="path_image" alt="polo">
            </figure>
            <div class="details-item">
                <h1 class="item_name">{{name_item}}</h1>
                <p class="item_price">S/. {{price_item}}</p>
                
                <p class="detail-text"><span class="bold-text">Marca:</span> {{name_marca}}</p>
                <p class="detail-text"><span class="bold-text">Talla:</span> {{talla_item}}</p>
                <!--
                <p class="detail-text"><span class="bold-text">Color:</span> {{name_cor}}</p>
                -->
                <!--
                <p class="detail-text"><span class="bold-text">Stock:</span> {{stock_item}}</p>
                -->
                
                <form id="div-cantItem">
                    <label for="cantItem">Cantidad:</label>
                    <input id="cantItem" type="number" name="cantItem" value="0" min="0">
                </form>

                <button type="button" class="btn btn-primary btn-sd" @click="doShop">
                    Comprar<img class="ficon" src="../assets/icons/shopcar.svg" alt="shopcar icon">
                </button>

                <!--
                <button  type="button" class="btn btn-primary btn-sd" @click="deleteCompras">
                    Delete 
                </button>
                -->

                <a class="bton-special anchor" :href="'/try-on/'+id_item"><button type="button" class="btn btn-primary btn-sd">Probar</button></a>
            </div>
            
        </div>

        <div class="description">
            <div class="name-description">
                <p>Description</p>    
            </div>
            <div class="body-description">
                <p>{{description_item}}</p>
            </div>

        </div>

    </div>
</template>

<script>
import axios from 'axios'

export default {
    name: 'NewItem',
    data(){
        return{
            cor_filters: [],
            marca_filters: [],
            talla_filters: [],
            name_cor: "",
            name_marca: "",
            description: "",
            path_image: "",
            description_item: "",
            name_item: "",
            price_item: "",
            talla_item: "",
            stock_item: "",
            id_item: "",
            compras: { "results" : []},
        }
    },
    mounted() {
        this.getFilters();
        this.getListShop();
        this.getInfo();
    },
    methods: {
        getFilters(){
            axios
                .get("/api/catalogo/filters/")
                .then(res => {
                    this.data = res.data;
                    this.cor_filters = this.data.colores;
                    this.marca_filters = this.data.marcas;
                    this.talla_filters = this.data.tallas;
                })
                .catch(error =>{
                    console.log(error)
                })
        },
        getInfo(){
            const item_id= this.$route.params.id;
            this.id_item = item_id;

            axios
                .get('/api/paginaItem/detailsPolo/'+item_id)
                .then(res => {
                    //console.log(res.data);
                    const data = res.data;
                    this.name_cor = this.cor_filters[data.color-1];
                    this.name_marca = this.marca_filters[data.marcas-1];
                    this.path_image = data.path_image;
                    this.description_item = data.description;
                    this.name_item = data.name_modelo;
                    this.price_item = data.price;
                    this.stock_item = data.stock;
                    this.talla_item = data.talla;
                })
                .catch(error => {
                    console.log(error);
                })
                
        },
        getListShop(){
            var items_shop = localStorage.getItem("startCompras");
            items_shop = JSON.parse(items_shop);
            console.log(items_shop);
            if(items_shop != null){
                this.compras["results"] = items_shop["results"];
            }
        },
        doShop(){
            const form = document.getElementById("cantItem");
            const detailsItem = {};

            detailsItem["id"] = this.id_item;
            detailsItem["modelo"] = this.name_item;
            detailsItem["marca"] = this.name_marca;
            detailsItem["talla"] = this.talla_item;
            detailsItem["precio"] = this.price_item;
            detailsItem["cantidad"] = form.value;
            detailsItem["get_image"] = this.path_image;

            this.compras["results"].push(detailsItem);

            localStorage.setItem("startCompras",JSON.stringify(this.compras));



            console.log(this.compras)


            /*const cantData = new FormData(form);

            for (var pair of cantData.entries()) {
                console.log(pair[0]+ ', ' + pair[1]); 
            }*/

        },
        deleteCompras(){
            localStorage.removeItem("startCompras");
        },

    },
}
</script>

<style scoped>

.item-body{
    background-color: #F2F5F9;
    padding-bottom: 50px;
}

.show-item{
    display: flex;
    justify-content: center;
    margin-bottom: 30px;
    padding-top: 30px;
}

.view-item{
    /*background-color: skyblue;*/
    width: 400px;
    margin-top: 25px;
    margin-bottom: 10px;
}

.image-item{
    max-width: 500px;
    min-width: 400px;
    width: 90%;
    padding-left: 70px;   
    padding-right: 10px;
}

.details-item{
    /*background-color: yellowgreen;*/
    width: 450px;
    padding-left: 60px;
    margin-top: 25px;
    margin-bottom: 10px;
    color: #0A043C;
}

.item_name{
    font-size: 50px;
    font-weight: bold;
}

.item_price{
    font-size: 40px;
    color: gray;
    font-weight: bold;
}

.detail-text{
    font-size: 20px;
}

.bold-text{
    font-weight: bold;
}

#cantItem{
    width: 80px;
    margin-left: 10px;
}


.btn-primary{
    width: 200px;
    margin-top: 20px;
    margin-left: 50px;
}

.ficon{
    margin-top: -5px;
    margin-left: 10px;
    width: 20px;
    filter: invert(1);
}


.description{
    width: 50vw;
    margin: auto;
    margin-bottom: 120px;
}

.name-description{
    background-color: #A3DDCB;
    border-radius: 10px;
}

.name-description p{
    color: black;
    font-weight: bold;
    font-size: 20px;
    padding: 10px 0;
    padding-left: 20px;
}

.body-description p{
    padding:0 20px;
}

</style>